.burger_constructor {
  padding-top: 100px;
}

.drop_zone > *:first-child, 
.drop_zone > *:last-child {
  margin-left: 32px;
}

.drop_zone > *:last-child {
  margin-top: 3px;
}

.ingredients {
  overflow-y: auto;
  max-height: calc(100vh - 90px - 40px - 40px - 56px - 40px - 40px - 64px - 64px - 40px);
  scrollbar-width: thin;
}

.order {
  margin-top: 40px;
  display: flex;
  gap: 40px;
  align-items: center;
  justify-content: flex-end;
}

.order_price {
  display: flex;
  align-items: center;
  gap: 10px
}